import React from 'react';
import Helmet from '../components/Helmet';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../style/main.scss';
import { useStaticQuery, graphql } from 'gatsby';
import PostDetail from '../components/PostDetail';
import Fade from 'react-reveal/Fade';

function Post({ pageContext }) {
  const { id } = pageContext;
  const data = useStaticQuery(
    graphql`
      query {
        allContentfulPost(sort: { fields: date, order: DESC }) {
          nodes {
            id
            images {
              fluid(maxWidth: 1000) {
                src
                ...GatsbyContentfulFluid
              }
            }
            videos {
              file {
                url
              }
            }
            title
            date(formatString: "yyyy / M")
            description {
              description
            }
            url
          }
        }
      }
    `
  );

  const node = data.allContentfulPost.nodes.find((n) => n.id == id);

  if (!node) return null;

  const { title } = node;

  return (
    <>
      <Helmet />
      <Fade right={false} bottom={true} duration={500} delay={250} distance="30px">
        <div className="postdetail-wrapper">
          <PostDetail node={node} />
        </div>
      </Fade>
    </>
  );
}

export default Post;
